  .lista{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column
  }

  .ListaForm{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-animation-name: aniDialogo;
            animation-name: aniDialogo;
    -webkit-animation-duration: .5s;
            animation-duration: .5s
  }
  
  .ListaForm .wrapper{
    border: 1px solid #af5c0e;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: auto 5%;
    background: #FFFFFF;
    position: absolute;
    width: 80%;
    padding: 5%;
    z-index:1;
    border-radius: 5px
  }
  
  .ListaForm .bg{
    width:100%;
    height:100%;
    position: absolute;
    top:0px;
    left:0px;
    background: #999999;
    opacity: 0.5;
  }

  @-webkit-keyframes aniLista{
    from{ opacity: 0; -webkit-transform:translateY(-10px); transform:translateY(-10px)}
    to{ opacity : 1; -webkit-transform:translateY(0px); transform:translateY(0px)}
  }

  @keyframes aniLista{
    from{ opacity: 0; -webkit-transform:translateY(-10px); transform:translateY(-10px)}
    to{ opacity : 1; -webkit-transform:translateY(0px); transform:translateY(0px)}
  }
  
  @-webkit-keyframes aniDialogo{
    from{ opacity: 0; -webkit-transform:scale(0.2); transform:scale(0.2)}
    to{ opacity : 1; -webkit-transform:scale(1); transform:scale(1)}
  }
  
  @keyframes aniDialogo{
    from{ opacity: 0; -webkit-transform:scale(0.2); transform:scale(0.2)}
    to{ opacity : 1; -webkit-transform:scale(1); transform:scale(1)}
  }
